import { Typography } from '@mui/material'
import React from 'react'
import { darkBg2 } from '../../../styles/colors'

interface Props {
  name: String
  value: String | number
}

const lineStyle = '1px solid ' + darkBg2

const separatorStyle = {
  borderBottom: lineStyle,
  marginBottom: 8,
  paddingBottom: 6
}

export const NameValue = (props: Props) => {
  const { name, value } = props

  let val
  if (!Boolean(value) && '' + value !== '0') {
    val = 'Not available'
  } else {
    val = value
  }
  return (
    <div style={separatorStyle}>
      <Typography variant="h3">{name}</Typography>
      <Typography variant="h2">{val}</Typography>
    </div>
  )
}
