import React from 'react'
import { Box, Typography, Avatar, CardHeader } from '@mui/material'
import { success, error, turmeric, superLight, errorGradient, successGradient, errorWarm, errorMedium, successFont } from '../../../styles/colors'
import { Step, StepResult, Test } from '../types'
import { formatUserfacingTestId as formatId, toDateTime, getStepInfo } from '../../../lib/StringUtils'
import { VerticalLine } from './common'
import { ImageMatchView } from '.'
import { BatchRunExportDataDirName } from '../../../config'

interface Props {
  test: Test
  failStepRef: React.MutableRefObject<null>
}

const stepBox: any = {
  display: 'flex',
  flexDirection: 'column',
  mt: '10px',
  justifyContent: 'center',
  alignItems: 'center',
  position: 'relative'
}

const stepType = {
  fontSize: '0.7rem',
  color: 'rgba(0, 0, 0, 0.6)'
}

const stepImageIcon: React.CSSProperties = {
  width: '3rem',
  height: '2.5rem'
  // margin: '11px'
}

const hBox: any = (org = true) => {
  return {
    position: 'absolute',
    bottom: 0,
    textAlign: 'center',
    width: '100%',
    borderRadius: '0 0 4px 4px'
  }
}

const lBox: any = (org = true) => {
  return {
    position: 'absolute',
    width: '600px',
    bottom: org ? '200px' : '0'
  }
}

// const lTypoStyle: any = {
//   text: { p: 1, float: 'left' },
//   date: { p: 1, float: 'right' }
// }
// const hTypoStyle: any = {
//   text: { pt: 1 },
//   date: { pb: 1 }
// }

const pStep = (step: Step, stepName: string) => (
  <React.Fragment>
    <span>{stepName}</span>
    <span style={stepType}>{` ID: ${formatId(step?.id)}`}</span>
  </React.Fragment>
)

export const StepsInfo = (props: Props) => {
  const { test, failStepRef } = props
  const testRun = test?.testRuns[test?.testRuns.length - 1]

  if (!testRun) {
    return null
  }
  const stepResults = [...testRun?.stepResults] // as we are updating stepResults later so to avoid updating original data
  const baseSrc = `./${BatchRunExportDataDirName}/${test.id}`
  const refList: React.RefObject<HTMLImageElement>[] = []
  const deviceData = testRun.deviceData
  const isLandscape =
    deviceData.currentRotation === deviceData.rotationVars.landscape || deviceData.currentRotation === deviceData.rotationVars.seascape
  console.log('test', test.title, 'testRun.dd', testRun.deviceData)
  let currSResult: StepResult | null = null
  let currStep: Step | null = null

  const stepImage = (name: string, style: React.CSSProperties) => (
      <React.Fragment>
        <img style={style} src={name} alt={test.id} />
      </React.Fragment>
    )

    // verify if there are any missing steps due to failed step
  ;(() => {
    const stepsCount = test?.steps?.length
    const stepRunsCount = stepResults?.length
    const missingStepRuns = stepsCount - stepRunsCount

    // copy the original steps to stepResults for rendering
    if (missingStepRuns > 0) {
      const startCopyFrom = stepsCount - missingStepRuns

      for (let i = startCopyFrom; i < stepsCount; i++) {
        // console.log('CopyingStep', test?.steps[i]);
        stepResults[i] = {} as any
        stepResults[i].step = test?.steps[i]
        stepResults[i].success = false
        stepResults[i].copy = true // just to keep record it's copied step
      }
    }
  })()

  // arrows to point out towards the next step
  const getArrows = (index: number) => {
    const nextStep = stepResults[index]
    if (!nextStep) return
    // const currStep = stepResults[index - 1]
    return <VerticalLine height="30px" />
  }

  // data to show on image hover
  const stepHoverDetail = (type: string, isLandscape = true) => {
    const isOriginal = type === 'org'
    const stepColor = currSResult?.success ? success : errorWarm // success in case if test passed
    const backgroundColor = isOriginal ? success : stepColor // original screenshot will always be success
    const lStyleType = isOriginal ? !!currSResult?.screenshot : false // landscape box positioning if testRun screenshot exist
    const hStyleType = isOriginal ? true : !currStep?.originalFrame // horizontal box left right prop if original screenshot exist
    const text = isOriginal ? 'Original' : 'Test Run'
    const date = isOriginal ? (currStep?.createdOn as string) : (currSResult?.createdOn as string)
    const boxStyle = isLandscape ? lBox(lStyleType) : hBox(hStyleType) // horizontal box or landscape box
    // const typoStyle = isLandscape ? lTypoStyle : hTypoStyle // horizontal typo or landscape

    return (
      <div className="info" style={{ ...boxStyle, backgroundColor, paddingTop: 10, height: 55 }}>
        <Typography variant="body1">{text} Screenshot</Typography>
        <Typography variant="body2">{toDateTime(date)}</Typography>
      </div>
    )
  }

  // show horizontal image & add hover detail
  const renderResult = (src: string, type: string) => {
    //const { roi } = calculateRoi(currStep as Step)

    const matches = type === 'org' ? currStep?.matches : currSResult?.matches

    return (
      <div className="matchResult" style={{ flexBasis: '50%', position: 'relative' }}>
        <ImageMatchView
          matchIndex={currStep?.matchIndex}
          matches={matches}
          selection={currStep?.selection}
          roi={currStep?.region}
          width={200}
          src={src}
        />

        {stepHoverDetail(type, false)}
      </div>
    )
  }

  const placeHolder = (type: any) => {
    return (
      <div
        style={{
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          flexGrow: 1,
          position: 'relative',
          backgroundColor: superLight,
          borderRadius: 4,
          minHeight: 300
        }}
      >
        <Typography>Not available</Typography>
        {stepHoverDetail(type, false)}
      </div>
    )
  }

  // show original and step run images
  const showStepImages = (index: number) => {
    const orgFrameSrc = `${baseSrc}/${currStep?.originalFrame}`
    const screenshotSrc = `${baseSrc}/testRuns/${testRun.deviceData?.serial}/${currSResult?.screenshot}`

    return (
      <div style={{ display: 'flex', flexDirection: isLandscape ? 'column' : 'row' }}>
        {currStep?.originalFrame ? renderResult(orgFrameSrc, 'org') : placeHolder('org')}
        <div style={{ height: 10, width: 10 }}></div>
        {currSResult?.screenshot ? renderResult(screenshotSrc, 'testRun') : placeHolder('testRun')}
      </div>
    )
  }

  const generateStep: any = (index: number) => {
    const stepName = getStepInfo(currStep as Step)
    const isSuccess = currSResult?.success
    // const stepBG = currSResult?.copy ? '#bdbdbd' : isSuccess ? success : error
    const ref: React.RefObject<HTMLImageElement> = React.createRef()
    const isCopy = currSResult?.copy
    const border = `0px solid ${!isSuccess ? error : turmeric}`

    refList.push(ref)

    return (
      <>
        <div
          ref={!isCopy && !isSuccess ? failStepRef : null}
          style={{
            minWidth: 600,
            maxWidth: 600,
            border: isCopy ? '' : border
          }}
        >
          <CardHeader
            avatar={
              <Avatar style={{ backgroundColor: isSuccess ? success : errorWarm }} aria-label="step-no">
                {stepName.icon && <stepName.icon />}
              </Avatar>
            }
            title={pStep(currStep as Step, stepName.primary)}
            subheader={stepName.secondary}
            action={currStep?.image && <>{stepImage(`${baseSrc}/${currStep?.image}`, stepImageIcon)}</>}
            style={{}}
          />

          <div
            style={{
              overflow: 'hidden',
              position: 'relative',
              borderBottom: '2px solid ' + superLight,
              paddingBottom: 20
            }}
          >
            {showStepImages(index)}
            {currSResult?.message && (
              <div
                style={{
                  borderRadius: 4,
                  padding: 10,
                  marginTop: 10,
                  background: isSuccess ? successGradient : errorGradient
                }}
              >
                <Typography variant="body2" sx={{ color: isSuccess ? successFont : errorMedium, whiteSpace: 'pre-wrap', fontFamily: 'monospace' }}>
                  {currSResult?.message}
                </Typography>
              </div>
            )}
          </div>

          {/* <CardActions disableSpacing></CardActions> */}
        </div>

        {getArrows(index + 1)}
      </>
    )
  }

  return (
    <>
      {stepResults.length &&
        stepResults.map((sResult, index: number) => {
          const opacity = sResult?.copy ? '0.5' : '1'
          currSResult = sResult
          currStep = sResult.step

          if (currStep.type === 'subTest') return null // this is an extra step recieving from repeato studio so skip it

          return (
            <Box className="stepCard" sx={{ ...stepBox, opacity }} key={index}>
              {generateStep(index)}
            </Box>
          )
        })}
    </>
  )
}
